#search-page {
    margin-bottom: 50rem;
}

#search-page .search-header {
    height: 90rem;
    padding: 0 25rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

#search-page .search-header .cancel {
    width: 200rem;
    font-size: 26rem;
    color: #4392e3;
}

#search-page .search-header .cancel img {
    width: 30rem;
    height: 30rem;
}

#search-page .container-fluid {
    padding-right: 40rem;
    padding-left: 40rem;
}

#search-page .from-to .control-label {
    padding-right: 40rem;
    font-size: 26rem;
    color: #444444;
    padding-top: 15rem;
    font-weight: 300;
    padding-left: 15rem;
}

#search-page .from-to .text-feild {
    padding-right: 0;
}

#search-page .from-to .form-control-feedback {
    right: 15rem;
    top: 15rem;
}

#search-page .from-to .form-control-feedback img {
    width: 20rem;
    height: 20rem;
}

#search-page .form-control-start {
    position: absolute;
    left: 40rem;
    z-index: 2;
    display: block;
    width: 34rem;
    height: 34rem;
    line-height: 34rem;
    text-align: center;
    pointer-events: none;
    top: 15rem;
}

#search-page .form-control-start img {
    width: 34rem;
    height: 34rem;
}

#search-page .from-to .form-control-start img {
    width: 34rem;
    height: 34rem;
}

#search-page .from-to .form-control-start {
    top: 10rem;
}

/* #search-page .from-to .form-control-start img{
    width: 40rem;
} */
#search-page .form-control-feedback {
    right: 40rem;
    width: 34rem;
    top: 14rem;
    height: 34rem;
    line-height: 34rem;
}

#search-page .form-control-feedback img {
    width: 34rem;
    height: 34rem;
}

#search-page .form-control {
    height: 65rem;
    border-radius: 50rem;
    padding: 0 100rem;
    font-size: 28rem;
    color: #444444;
    background-color: #eaeaea;
}

#search-page .from-to .form-control {
    padding: 0 10rem 0 90rem;
}


#search-page .form-group {
    font-size: 24rem;
    color: #444444
}

#search-page .tab {
    padding-top: 60rem;
}

#search-page .nav-tabs > li > a {
    font-size: 25rem;
    color: #999999;
}

#search-page .nav-tabs > li.active > a, .nav-tabs > li.active > a:focus, .nav-tabs > li.active > a:hover {
    color: #444444;
    border: none;
    border-bottom: 4rem solid #28cbd5;
}

#search-page .nav-tabs > li {
    width: 25%;
    text-align: center;
}

#search-page .nav-tabs > li a {
    padding-left: 0;
    padding-right: 0;
}

/* ------------------------------------------ */
#search-page .search_top {
    padding: 20rem 0 40rem 0;
}

#search-page .search_top .cap {
    border-top: 1rem solid #d3d3d3;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 20rem 40rem;
    position: relative;
    height: 70rem;
    background-color: #fcfcfc;
    border-bottom: 1rem solid #f3f3f3;
}

#search-page .search_top .cap .title {
    font-size: 30rem;
    color: #999999;
    font-weight: 500;
}

#search-page .search_top .cap img {
    position: absolute;
    right: 40rem;
    width: 30rem;
    height: 30rem;
}

#search-page .search_top .history {
    padding: 0 40rem;
}

#search-page .search_top .history .history_items {
    padding: 25rem 0;
    color: #555555;
    font-size: 30rem;
}

#search-page .search_top .history .history_items img {
    width: 32rem;
    height: 32rem;
    margin-right: 30rem;
}

#search-page .shcedule_numbeer {
    font-size: 44rem;
    color: #555555;
    padding: 40rem 0 0 31rem;
}

#search-page .shcedule_numbeer span {
    font-size: 25rem;
}

#search-page .location {
    padding: 20rem 40rem 20rem 40rem;
    text-align: center;
}

#search-page .location span {
    margin-left: 20rem;
    color: #555555;
    font-size: 26rem;
    vertical-align: middle;
}

#search-page .location .location-ic {
    width: 47rem;
    height: 48rem;
}

#search-page .location {
    text-align: center;
    width: 100%;
    height: 65rem;
    padding: 0;
    margin-top: -25rem;
}


