#login .ground-button {
    margin-top: 30rem;
}

#login .cancel {
    padding: 0 40rem;
}

#login .cancel button   {
    text-align: left;
    background-color: #fff;
    padding: 0 40rem;
    height: auto;
}

#login .logo{
    text-align: center;
}

#login form.field{
    margin-top: 18rem;
}

#login .field{
    padding: 10rem 50rem 0 50rem;
}

#login .forgetPass{
    text-align: left;
}

#login .form-control{
    height: 100rem;
    border: none;
    border-bottom: 1rem solid #e8e7e6;
    -webkit-box-shadow: none;
            box-shadow: none;
    padding: 0;
    font-size: 29rem;
}

#login .form-control::-webkit-input-placeholder {
    font-size: 28rem;
    color: #cccccc;
}

#login .form-control:-ms-input-placeholder {
    font-size: 28rem;
    color: #cccccc;
}

#login .form-control::-ms-input-placeholder {
    font-size: 28rem;
    color: #cccccc;
}

#login .form-control::placeholder {
    font-size: 28rem;
    color: #cccccc;
}

#login .form-control:focus {
    border-bottom: 1rem solid #00b1c3;
    -webkit-box-shadow: none;
            box-shadow: none;
}

#login .help{
    font-size: 26rem;
    color: #4392e3;
    padding-top: 20rem;
    /* display: flex; */
    /* align-items: center; */
    position: relative;
    text-align: center;
}
#login .help .createAccount{
    /* position: absolute; */
    /* right: 0; */
    padding-top: 15rem;
}

#login .help span{
    padding-right: 15rem;
}

#login button{
    font-size: 31rem;
    color: #fff;
    height: 94rem;
    border: none;
    border-radius: 50rem;
    width: 94%;
}

#login .btn-m,
#login .fb{
    padding: 20rem 60rem 0 60rem;
}

#login .btn-m{
    text-align: center;
}

#login .btn-m button{
    background-image: -webkit-gradient(linear, left top, right top, from(#28cbd5) , to(#5ceadc));
    background-image: -webkit-linear-gradient(left, #28cbd5 , #5ceadc);
    background-image: -o-linear-gradient(left, #28cbd5 , #5ceadc);
    background-image: linear-gradient(to right, #28cbd5 , #5ceadc);
}

#login .btn-red button{
    background-image: -webkit-gradient(linear, left top, right top, from(#d52845) , to(#ea5c5c));
    background-image: -webkit-linear-gradient(left, #d52845 , #ea5c5c);
    background-image: -o-linear-gradient(left, #d52845 , #ea5c5c);
    background-image: linear-gradient(to right, #d52845 , #ea5c5c);
}

#login .fb{
    text-align: center;
}

#login .fb button{
    background-image: -webkit-gradient(linear, left top, right top, from(#4467b1) , to(#7298e8));
    background-image: -webkit-linear-gradient(left, #4467b1 , #7298e8);
    background-image: -o-linear-gradient(left, #4467b1 , #7298e8);
    background-image: linear-gradient(to right, #4467b1 , #7298e8);
}

#login .fb button span{
    padding-right: 15rem;
}

#login .logo-ic {
    width: 100rem;
    height: 143rem;
}

#login .help-ic {
    width: 48rem;
    height: 48rem;
}

#login .facebook-ic {
    width: 50rem;
    height: 50rem;
}
#login .content-login{
    width: 100%;
    top: 140rem;
    position: absolute;
}


