#ResetPassMail {
    width: 90%;
    top: 90rem;
    position: relative;
    background-color: #fff;
    padding: 20rem 0 0 0;
    height: 950rem;
    border-radius: 8rem;
    margin: auto;
}

#ResetPassMail .cancel button   {
    text-align: left;
    background-color: #fff;
    padding-top: 0rem;
    border: none;
    -webkit-box-shadow: none;
            box-shadow: none;
}
#ResetPassMail .cancel button img{
    width: 130rem;
    height: 29rem;
}
#ResetPassMail .logo{
    padding-top: 80rem;
    text-align: center;
}

#ResetPassMail .help{
    font-size: 26rem;
    color: #4392e3;
    padding-top: 55rem;
}
#ResetPassMail  .help span{
    padding-right: 15rem;
}
#ResetPassMail  .help img{
    height: 48rem;
    width: 48rem;
}

#ResetPassMail .content{
    font-size: 32rem;
    color: #555555;
    padding-top: 5rem;
    padding-top: 88rem;
}
#ResetPassMail .title{
    font-size: 36rem;
    color: #1a1406;
    padding-top: 5rem;
    text-align: center;
    font-weight: 600;
    padding-top: 55rem; 
}






