#edit_personal {
    padding-bottom: 50rem;
}
#edit_personal .edit_personal_header{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 20rem 0 30rem 19rem;
    border-bottom: 1rem solid #d3d3d3;
}
#edit_personal .edit_personal_title{
    position: relative;
    -ms-flex-align: center;
        align-items: center;
    height: 165rem;
    background-color: #00b1c3;
}
#edit_personal .edit_personal_title .title{
    font-size: 36rem;
    color: #fefefe;
    font-weight: 500;
    bottom: 28rem;
    position: absolute;
}

#edit_personal .cancel button   {
    width: 20rem;
    height: 36rem;
    text-align: left;
    background-color: #fff;
    padding-top: 0rem;
}
#edit_personal .cancel button  img {
    width: 20rem;
    height: 36rem;
    
}
#edit_personal .title{
    font-size: 36rem;
    color: #1a1406;
    text-align: center;
    width: 100%;
    font-weight: 500;
}
#edit_personal button{
    font-size: 36rem;
    font-weight: 600;
    border: none;
    border-radius: 100rem;
    width: 560rem; 
}


#edit_personal .edit_personal_content{
    padding: 0 40rem 60rem 40rem;
    border-bottom: 1rem solid #d3d3d3;
}

#edit_personal .edit_personal_content .menu{
    padding-top: 40rem;
    font-size: 30rem;
    color: #555555;
}

#edit_personal .edit_personal_content span{
    top: 48rem;
    right: 0;
}
#edit_personal .edit_personal_content span img{
    width: 15rem;
    height: 26rem;
}




