.rec-user {
  padding-left: 40rem;
  min-height: 450rem;
}

.rec-user .cap-rec-user {
  height: 100rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 30rem;
  padding-right: 20rem;
  color: #999999;
}

.rec-user .cap-rec-user div:first-child {
  -ms-flex: 1 1;
      flex: 1 1;
}

.card-user {
  height: 280rem;
  width: 240rem;
  border: 1rem solid #d3d3d3;
  text-align: center;
  padding-top: 20rem;
  margin-right: 30rem;
}

.card-user .name {
  font-size: 30rem;
  color: #444444;
  padding-top: 20rem;
}

.card-user .nick-name {
  font-size: 24rem;
  color: #bbbbbb;
}
.card-user .nick-name img{
  width: 83rem;
  height: 26rem;
}

.rec-user .user-avatar {
  width: 152rem;
  height: 150rem;
  border-radius: 50%;
}

.cap-rec-user{
  position: relative;
}

.rec-user .cap-rec-user .loadMore{
  position: absolute;
  right: 20rem;
}

.rec-user .cap-rec-user .loadMore img{
  width: 24rem;
  height: 28rem;
  margin-top: -9rem;
  margin-left: 10rem;
}


