.search_account{
    padding: 0 40rem;
}
.search_account .card_user{
    padding: 20rem 0 20rem 0;
}
.search_account .card_user .user_icon img{
    width: 100rem;
}
.search_account .card_user .name{
    color:#444444;
    padding-top: 1rem;
}
.search_account .card_user .name p{
    font-size: 25rem;
    color: #bbbbbb;
}
.search_account .card_user .name p span{
    padding-right: 12rem;
}
.search_account .card_user .name p span img{
    width: 85rem;
}
.search_account .card_user .inner{
    display: -ms-flexbox;
    display: flex;
    float: right;
    font-size: 28rem;
    color:#999999;
}
.search_account .card_user .inner .fan{
    padding-right:25rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.search_account .card_user .inner .spot img{
    margin-bottom: -1rem;
}

.search_account .card_user .inner .fan span,
.search_account .card_user .inner .post span{
    display: inline;
    margin-bottom: -10rem;
}

.search_account .card_user .inner .post span{
    padding-top: 5rem;
    display: -ms-flexbox;
    display: flex;
    float: right;
}

.search_account .card_user .inner img{
    padding-right:6rem;
    width: 38rem;
}
