.modal-page {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    width: 100vw;
    bottom: 116rem;
    left: 0;
    /* height: 100%; */
    height: calc(100% - 116rem);
}

.modal-page-content {
    /* display: flex; */
    -ms-flex: 1 1;
        flex: 1 1;
}

.modal-hide {
    top: 100vh;
    -webkit-transition-duration: 0.5s;
         -o-transition-duration: 0.5s;
            transition-duration: 0.5s;
    background: #ffffff;
}

.modal-show {
    -webkit-transition-duration: 0.5s;
         -o-transition-duration: 0.5s;
            transition-duration: 0.5s;
    top: 0;
    background: #ffffff;
    z-index: 1000;
}
.h-100{
    height:100%;
    background-color: rgba(000, 000, 000, 0.5);
}



