#mail_pass {
    padding-bottom: 50rem;
    font-weight: normal;
}
#mail_pass .mail_pass_header{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 20rem 40rem 20rem 20rem;
    border-bottom: 1rem solid #d3d3d3;
}

#mail_pass .cancel  img{
    width: 20rem;
    height: 36rem;
}
#mail_pass .title{
    font-size: 36rem;
    color: #1a1406;
    text-align: center;
    width: 100%;
    font-weight: 600;
}

#mail_pass .cap{
    padding: 45rem 40rem 22rem 40rem;
    font-size: 28rem;
    color: #999999;
    border-bottom: 1rem solid #f3f3f3;
    background-color: #fcfcfc;
}
#mail_pass .field{
    padding: 20rem 40rem 0 40rem;
}
#mail_pass .mailadress .form-control{
    border: none;
    border-bottom: 3rem solid #e8e7e6;
    -webkit-box-shadow: none;
            box-shadow: none;
    color: #555555;
    border-radius: 0;
    padding: 0;
}
#mail_pass .mailadress .text{
    font-size: 28rem;
    color: #666666;
    padding: 48rem 40rem 60rem 40rem;
    border-bottom: 1rem solid #d3d3d3;
}

#mail_pass .password .field{
    padding: 20rem 40rem 0 40rem;
}
#mail_pass .password .form-control{
    height: 100rem;
    border: none;
    border-bottom: 3rem solid #e8e7e6;
    -webkit-box-shadow: none;
            box-shadow: none;
    border-radius: 0;
    padding: 0;
}
#mail_pass .password .form-control::-webkit-input-placeholder {
    color: #cccccc;
}
#mail_pass .password .form-control:-ms-input-placeholder {
    color: #cccccc;
}
#mail_pass .password .form-control::-ms-input-placeholder {
    color: #cccccc;
}
#mail_pass .password .form-control::placeholder {
    color: #cccccc;
}
#mail_pass .form-control:focus {
    border-bottom: 2rem solid #00b1c3;
    -webkit-box-shadow: none;
            box-shadow: none;
}
#mail_pass .password .text{
    font-size: 28rem;
    color: #555555;
    padding: 48rem 40rem 60rem 40rem;
    border-bottom: 1rem solid #d3d3d3;
}
#mail_pass .password .form-control-feedback{
    bottom: 28rem;
    top: auto;
}
#mail_pass .password .form-control-feedback img{
    width: 34rem;
    height: 34rem;
}

#mail_pass .help{
    font-size: 26rem;
    color: #4392e3;
    padding-top: 25rem;
}
#mail_pass .help span{
    padding-right: 15rem;
}
#mail_pass .help img{
    width: 48rem;
    height: 47rem;
}
#mail_pass .btn-m button{
    font-size: 34rem;
    color: #fff;
    height: 94rem;  
    border: none;
    border-radius: 100rem;
    width: 560rem;
}
#mail_pass .btn-m{
    text-align: center;
    padding:60rem 80rem 0 80rem;
}
#mail_pass .btn-m button{
    background-image: -webkit-gradient(linear, left top, right top, from(#28cbd5) , to(#5ceadc));
    background-image: -webkit-linear-gradient(left, #28cbd5 , #5ceadc);
    background-image: -o-linear-gradient(left, #28cbd5 , #5ceadc);
    background-image: linear-gradient(to right, #28cbd5 , #5ceadc);
}

#mail_pass .form-group {
    position: relative;
}


