    text-align: right;
}
.myuser .card_schedule .private span{
    padding-left: 5rem;
}
.myuser .card_schedule .private img{
    width: 100rem;
    height: 100rem;
}

.myuser .card_schedule .prince_icon{
    font-size: 32rem;
