}

.myuser .entry{
    padding-top: 70rem;
    text-align: center;
    padding-bottom: 45rem;
}


.myuser .entry .title{
    font-size: 28rem;
    color: #444444;
}

.myuser .entry .list {
    background-color: #dddddd;
    width: 550rem;
