    color: #4392e3;
}

.myuser .ubility{
    padding-top: 80rem;
    padding-bottom: 60rem;
    position: relative;
}
.myuser .ubility row{
    margin-left: 0;
    margin-right: 20rem;
}

.myuser .ubility .control-label{
    padding-right: 40rem;
