html {
  font-size: 0.130208333vw;
}

input::-webkit-input-placeholder{
  font-family: "Kozuka Gothic Pr6N", Helvetica, Arial, sans-serif
}

input:-ms-input-placeholder{
  font-family: "Kozuka Gothic Pr6N", Helvetica, Arial, sans-serif
}

input::-ms-input-placeholder{
  font-family: "Kozuka Gothic Pr6N", Helvetica, Arial, sans-serif
}

input::placeholder{
  font-family: "Kozuka Gothic Pr6N", Helvetica, Arial, sans-serif
}

.container {
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0;
  margin: 0;
}

.content {
  -ms-flex: 1 1;
      flex: 1 1;
  overflow-y: scroll;
}

.menu {
  background: #ffffff;
  -ms-flex: 1 1;
      flex: 1 1;
  height: 100vh;
}
a:focus, a:hover{
  text-decoration: none;
}
[class*="col-"] {
  padding-left: 15rem;
  padding-right: 15rem;
  min-height: 1rem;
}
.d_flex_center{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
/* form */
.form-control-feedback{
  width: auto;
  height: auto;
  line-height: 34rem;
}
.form-horizontal .form-group {
  margin-right: -15rem;
  margin-left: -15rem;
}
.form-group {
  margin-bottom: 15rem;
}
label {
  margin-bottom: 5rem;
}
.row {
  margin-right: -15rem;
  margin-left: -15rem;
}
.form-control{
  border-radius: 0;
}

.cancel button img,
.cancel-ic {
  width: 30rem;
  height: 30rem;
}

.modal-custom{
  width: 90%;
  top: 90rem;
  position: relative;
  background-color: #fff;
  padding: 20rem 0 0 0;
  height: 950rem;
  border-radius: 8rem;
  margin: auto;
}

.modal-page .search_header{
  height: 80rem;
  padding-top: 32rem;
  display: -ms-flexbox;
  display: flex;
}

.search_header .cancel img{
  width: 30rem;
  height: 30rem;
}
