/* ------------------------------------------------------- */
.search_schedule{}
.search_schedule .schedule_item{
    padding-top: 23rem;
    display: -ms-flexbox;
    display: flex;
}

.search_schedule .schedule_item .left{
    color: #333333;
    font-size: 52rem;
    text-align: center;
    width: 130rem;
}
.search_schedule .schedule_item .left .inSession{
    font-size: 24rem;
    font-weight: bold;
    color: #ffffff;
    background-color: #28cbd5;
    padding: 4rem 5rem 0 5rem;
    margin: 0 13rem;
}
.search_schedule .schedule_item .right{
    padding-right: 40rem;
    width: 100%;
}
.search_schedule .schedule_item .gray{
    font-size: 24rem;
}
.search_schedule .schedule_item .schedule_content{
    font-size: 30rem;
    color: #555555;
}
.search_schedule .schedule_item .schedule_content .name{
    padding: 22rem 0;
}
.search_schedule .schedule_item .schedule_content .name .row{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.search_schedule .schedule_item .schedule_content .name .private img{
    padding-right: 12rem;
}
.search_schedule .schedule_item .schedule_content .name .private{
    text-align: right;
}
.search_schedule .schedule_item .schedule_content .name .fullname{
    font-size: 26rem;
    color: #444444;
}

.search_schedule .schedule_item .schedule_content .name .username{
    font-size: 22rem;
    color: #bbbbbb;
}


.search_schedule .schedule_item .schedule_content .img{
    padding-top: 20rem;
}
.search_schedule .schedule_item .schedule_content .img>img{
    width: 100%;
}
.search_schedule .schedule_item .schedule_content .prince_icon{
    font-size: 32rem;
    border-top: 1rem solid #e9e9e9;
}
.search_schedule .schedule_item .schedule_content .prince_icon .prince{
    color: #e14a56;
}
.search_schedule .schedule_item .schedule_content .prince_icon > .row{
    padding-top: 34rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
}


.search_schedule .schedule_item .schedule_content .schedule .title{
    font-size:  26rem;
    color: #999999;
}
.search_schedule .schedule_item .schedule_content .schedule .content{
    font-size:  30rem;
    color: #555555;
    padding-left: 40rem;
    margin-top: 20rem;
    border-left: 4rem solid #28cbd5;
}
.search_schedule .schedule_item .schedule_content .schedule .content .date{
    font-size: 44rem;
    color: #333333;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 20rem;
}
.search_schedule .schedule_item .schedule_content .schedule .content .date .from{
    padding-right: 15rem;
}
.search_schedule .schedule_item .schedule_content .schedule .content .date .to{
    padding-left: 20rem;
}
.search_schedule .schedule_item .schedule_content .schedule .content .date span{
    font-size: 30rem;
}
.search_schedule .blue{
    color: #5e95d4;
}
.search_schedule .red{
    color: #e14a56;
}
.search_schedule .gray{
    color: #999999;
}

.search_schedule .schedule_item .schedule_content .schedule .content .house{
    font-size: 32rem;
    color: #999999;
    padding-top: 20rem;
}
.search_schedule .schedule_item .schedule_content .schedule .content .house span{
    font-size: 24rem;
}
.search_schedule .delete-ic {
    width: 32rem;
    height: 32rem;
}

.search_schedule .dropdown-ic {
    width: 22rem;
    height: 18rem;
}

.search_schedule .user-ic {
    width: 71rem;
    height: 71rem;
}

.search_schedule .public-ic {
    width: 49rem;
    height: 37rem;
}

.search_schedule .download-ic {
    width: 46rem;
    height: 46rem;
}

.search_schedule .like-ic {
    width: 46rem;
    height: 47rem;
}

.search_schedule .share-ic {
    width: 47rem;
    height: 47rem;
}



