#calendarSetting .header{
    height: 360rem;
    padding:35rem 40rem 0 40rem;
    background-image: url(../../../icons/img_header.png) ;
    position: relative;
    background-position: center;
    background-attachment: initial;
    background-size: cover;
}
#calendarSetting .header .cancel{
    font-size: 26rem;
    color: #4392e3;
    position: absolute;
    top: 35rem;
}
#calendarSetting .header .cancel img{
    width: 20rem;
    height: 36rem;
}
#calendarSetting .header .title{
    font-size: 36rem;
    text-align: center;
    color: #fefefe;
    position: absolute;
    bottom: 47rem;
}


#calendarSetting .container-fluid{
    padding-right: 40rem;
    padding-left: 40rem; 
}
#calendarSetting .user{
    padding-top: 30rem;
    display: -ms-flexbox;
    display: flex;
}
#calendarSetting .user .user-icon img{
    width: 72rem;
    height:72rem;
}
#calendarSetting .user .name{
    font-size: 26rem;
    color: #444444;
    padding-top: 10rem;
    padding-left: 22rem;
}
#calendarSetting .user .name p{
    font-size: 22rem;
    color: #bbbbbb;
    margin-top: -5rem;

}

#calendarSetting .edit{
    font-size: 30rem;
    color: #555555;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 60rem 40rem 45rem 40rem; 
    border-bottom: 1px solid #d3d3d3;
}
#calendarSetting .edit .ic_edit{
    width: 49rem;
    height: 47rem;
    margin-right: 30rem;
}
#calendarSetting .edit .arrow{
    position: absolute;
    right: 40rem;
    width: 14rem;
    height: 26rem;
}


#calendarSetting .color{
    font-size: 30rem;
    color: #555555;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #d3d3d3;
    -ms-flex-align: center;
        align-items: center;
    padding: 40rem; 
}
#calendarSetting .color .ic_color{
    width: 25rem;
    height: 25rem;
    margin-right: 47rem;
    margin-left: 10rem;
}
#calendarSetting .color .arrow{
    position: absolute;
    right: 40rem;
    width: 14rem;
    height: 26rem;
}

#calendarSetting .alarm{
    padding: 40rem  ;
}
#calendarSetting .alarm .title{
    color:#999999;
    font-size: 30rem;
}
#calendarSetting .alarm .menu{
    padding-top: 40rem;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
#calendarSetting .alarm .menu .ic_edit{
    width: 36rem;
    height: 36rem;
    margin-right: 30rem;
}
#calendarSetting .alarm .menu .arrow{
    position: absolute;
    right: 0rem;
    width: 14rem;
}



