#calendar_regis {
    padding-bottom: 50rem;
}
#calendar_regis .calendar_regis_header{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 20rem 40rem;
    border-bottom: 1rem solid #d3d3d3;
    position: relative;
}
#calendar_regis .calendar_regis_title{
    position: relative;
    -ms-flex-align: center;
        align-items: center;
    height: 165rem;
    background-color: #00b1c3;
}
#calendar_regis .calendar_regis_title .title{
    font-size: 36rem;
    color: #fefefe;
    font-weight: 500;
    bottom: 28rem;
    position: absolute;
}

#calendar_regis .cancel button   {
    width: 20rem;
    height: 36rem;
    text-align: left;
    background-color: #fff;
    position: absolute;
    bottom: 37rem;
    padding-top: 0rem;
}
#calendar_regis .cancel button img{
    width: 20rem;
    height: 36rem;
}
#calendar_regis .title{
    font-size: 36rem;
    color: #1a1406;
    text-align: center;
    width: 100%;
    font-weight: 600;
}

#calendar_regis .feild{
    padding: 0 40rem 60rem 40rem;
    border-bottom: 1rem solid #d3d3d3;
}
#calendar_regis .feild .form-control{
    padding: 0 0 0 90rem;
}
#calendar_regis .feild .form-control-start{
    font-size: 32rem;
    font-weight: 600;
    color: #222222;
    text-align: left;
}
#calendar_regis .feild .form-control-start img{
    height: 48rem;
    width: 48rem;
}
#calendar_regis .feild .form-control{
    font-size: 32rem;
    font-weight: 300;
    color: #555555;
}


#calendar_regis .text{
    font-size: 26rem;
    color: #555555;
    padding: 60rem 40rem 0 40rem;
    text-align: justify;
}


#calendar_regis .mailadress .form-control{
    height: 100rem;
    border: none;
    border-bottom: 2rem solid #e8e7e6;
    -webkit-box-shadow: none;
            box-shadow: none;
    font-size: 32rem;
    color: #555555;
    border-radius: 0;
}

#calendar_regis  .form-control{
    height: 100rem;
    border: none;
    border-bottom: 3rem solid #e8e7e6;
    -webkit-box-shadow: none;
            box-shadow: none;
    font-size: 30rem;
    color: #999999;
    border-radius: 0;
}
#calendar_regis  .form-control::-webkit-input-placeholder {
    color: #cccccc;
}
#calendar_regis  .form-control:-ms-input-placeholder {
    color: #cccccc;
}
#calendar_regis  .form-control::-ms-input-placeholder {
    color: #cccccc;
}
#calendar_regis  .form-control::placeholder {
    color: #cccccc;
}
#calendar_regis .form-control:focus {
    border-bottom: 2rem solid #00b1c3;
    -webkit-box-shadow: none;
            box-shadow: none;
}

#calendar_regis  .form-control-feedback{
    bottom: 15rem;
    right: 0rem;
    top: auto;
}
#calendar_regis  .form-control-feedback img{
    width: 92rem;
    height: 63rem;
}


#calendar_regis button{
    font-size: 36rem;
    font-weight: 600;
    color: #fff;
    height: 94rem;  
    border: none;
    border-radius: 100rem;
    width: 560rem; 
}
#calendar_regis .btn-m{
    text-align: center;
    padding:60rem 80rem 0 80rem;
}
#calendar_regis .btn-m button{
    background-image: -webkit-gradient(linear, left top, right top, from(#28cbd5) , to(#5ceadc));
    background-image: -webkit-linear-gradient(left, #28cbd5 , #5ceadc);
    background-image: -o-linear-gradient(left, #28cbd5 , #5ceadc);
    background-image: linear-gradient(to right, #28cbd5 , #5ceadc);
}

#calendar_regis .form-group {
    position: relative;
}
#calendar_regis .form-control-start {
    position: absolute;
    left: 0;
    z-index: 2;
    display: block;
    width: 34rem;
    height: 34rem;
    line-height: 34rem;
    text-align: center;
    pointer-events: none;
    bottom: 34rem;
}




